import { http } from '../../../utils/http';
import { setMessage } from '../MessageActions';
import {
    GET_ALL_BULK_START,
    GET_ALL_BULK_SUCCESS,
    GET_ALL_BULK_ERROR,
    GET_BULK_START,
    GET_BULK_SUCCESS,
    GET_BULK_ERROR
} from '../../constants';

const getBulkStart = () => {
    return { type: GET_BULK_START };
};
const getBulkSuccess = (payload) => {
    return { type: GET_BULK_SUCCESS, payload };
};
const getBulkError = (error) => {
    return { type: GET_BULK_ERROR, error };
};

const getAllBulkStart = () => {
    return { type: GET_ALL_BULK_START };
};
const getAllBulkSuccess = (payload) => {
    return { type: GET_ALL_BULK_SUCCESS, payload };
};
const getAllBulkError = (error) => {
    return { type: GET_ALL_BULK_ERROR, error };
};

const bulkPlaceOrder = (payload, successCB) => (dispatch) => {
    try {
        http(process.env.REACT_APP_ROOT_URL_ORDER, 'POST', 'orders-service/bulk/order', payload, true)
            .then((res) => {
                dispatch(setMessage(res?.message));
                successCB && successCB(res);
            })
            .catch((err) => {
                console.log(err);
            });
    } catch (error) {
        console.log(error);
    }
};

const bulkReturnBook = (payload) => (dispatch) => {
    try {
        http(process.env.REACT_APP_ROOT_URL_ORDER, 'POST', 'orders-service/bulk/book/return', payload, true)
            .then((res) => {
                dispatch(setMessage(res?.message));
            })
            .catch((err) => {
                console.log(err);
            });
    } catch (error) {
        console.log(error);
    }
};

const bulkUpdateOrders = (payload, successCB) => (dispatch) => {
    try {
        http(process.env.REACT_APP_ROOT_URL_ORDER, 'PUT', 'orders-service/bulk/order/status', payload, true)
            .then((res) => {
                dispatch(setMessage(res?.message));
                successCB && successCB(res);
            })
            .catch((err) => {
                console.log(err);
            });
    } catch (error) {
        console.log(error);
    }
};

const getBulkProcess = (id) => (dispatch) => {
    dispatch(getBulkStart());
    try {
        http(process.env.REACT_APP_ROOT_URL_ORDER, 'GET', `orders-service/bulk/${id}`, true)
            .then((res) => {
                const payload = {
                    bulkData: res
                };
                dispatch(getBulkSuccess(payload));
            })
            .catch((err) => {
                dispatch(getBulkError(err));
            });
    } catch (error) {
        dispatch(getBulkError('Something went wrong'));
    }
};

const getAllBulkProcess = (rowsPerPage = 10, page = 1) => (dispatch) => {
    dispatch(getAllBulkStart());
    try {
        http(process.env.REACT_APP_ROOT_URL_ORDER, 'GET', `orders-service/bulk?pageNumber=${page}&pageSize=${rowsPerPage}`, true)
            .then((res) => {
                const payload = {
                    allBulkData: res
                };
                dispatch(getAllBulkSuccess(payload));
            })
            .catch((err) => {
                dispatch(getAllBulkError(err));
            });
    } catch (error) {
        dispatch(getAllBulkError('Something went wrong'));
    }
};

export { bulkPlaceOrder, bulkUpdateOrders, getBulkProcess, getAllBulkProcess, bulkReturnBook };
